/* eslint-disable */
import './App.css';
import { Navbar,Nav,NavDropdown,Button,Jumbotron } from 'react-bootstrap';
import { useState } from 'react';
import Data from './Data';
import { render } from '@testing-library/react';
import Detail from './Detail';
import axios from 'axios';

import { Link, Route, Switch } from 'react-router-dom';


function App() {

  let [shoes, shoes변경] = useState(Data);
  let [재고, 재고변경] = useState([10,11,12]);



  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
        <Navbar.Brand href="#home">cat shop</Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ml-auto">
            <Nav.Link as={Link} to="/">Home</Nav.Link>
            <Nav.Link as={Link} to="/detail">디테일</Nav.Link>
            <NavDropdown title="Dropdown" id="basic-nav-dropdown">
              <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.2">Another action</NavDropdown.Item>
              <NavDropdown.Item href="#action/3.3">Something</NavDropdown.Item>
              <NavDropdown.Divider />
              <NavDropdown.Item href="#action/3.4">Separated link</NavDropdown.Item>
            </NavDropdown>
          </Nav>
        </Navbar.Collapse>
      </Navbar>

        <Route exact  path='/' >
            <Jumbotron className="background">
              <h1>20% season Off</h1>
              <p>
                This is a simple hero unit, a simple jumbotron-style component for calling
                extra attention to featured content or information.
              </p>
              <p>
                <Button variant="primary">Learn more</Button>
              </p>
            </Jumbotron>

            <div className="container">
              <div className="row">          
                {
                  shoes.map(
                    function(a, id){
                      return(
                        <Card  shoes={shoes[id]}  i={id} key={id} />
                      )
                    }
                  )
                }
              </div>
              <button className="btn btn-primary" onClick={()=>{
                


                // axios.post('서버url',{id: 'codingapple', pw: 1234})

                axios.get('https://codingapple1.github.io/shop/data2.json') //1.axios.get(데이터 요청할 url)
                .then((result)=>{
                  console.log(result.data);
                  shoes변경( [...shoes, ...result.data] );

                }) //2.성공하면 then((가져온자료)=>{가져온자료.선택할자료})
                .catch((e)=>{
                  console.log(e)
                }) //3.실패하면 catch
              }}>더보기</button>
            </div>
          
        </Route>
        {/* // 아무문자나 받겠다는 url작명법(url파라미터 문법) */}
        <Route path="/detail/:id"> 
            <Detail shoes={shoes} 재고={재고} 재고변경={재고변경}/>
        </Route>
          
        <Route path="/:id"> 
        {/* '/모든문자' 라는 경로를 의미한다. */}
          <div>아무거나 적었을 때 이거 보여주세요</div>
        </Route>

    </div>
  );
}



function Card(props){
  return(
    <div className="col-md-4">
      <img src={ 'https://codingapple1.github.io/shop/shoes' + (props.i+1) + '.jpg' } width="100%"/>
      {/* 템플릿 리터럴 왜 안되냐 */}
      {/* <img src={ `'https://codingapple1.github.io/shop/shoes'${props.i+1}'.jpg}'` } width="100%"/>  */}
      <h4>{props.shoes.title}</h4>
      <p>{props.shoes.content} {props.price}</p>
    </div>    
  )
}

export default App;
