import axios from 'axios';
import React, { useState, useEffect } from 'react';
import { useHistory, useParams } from 'react-router';
import styled from 'styled-components';
import './Detail.scss';





let 박스 = styled.div`
 padding: 20px;
`;

let 제목 = styled.h4`
    font-size : 25px;
    color : ${ props => props.색상 };
`;

function Detail(props){

    let [alert, setAlert] = useState(true);
    let [inputData, setInputData] = useState('');
    let [order, setOder] = useState();

    useEffect(()=>{        
        let 타이머 = setTimeout(() => { setAlert(false) }, 2000);
        console.log('안녕')
        return ()=>{clearTimeout(타이머)} 
    },[alert]);

    // useEffect훅 1. 컴포넌트가 mount되었을 때 컴포넌트가 update될 때, 특정 코드를 실행할 수 있음 
    // useEffect훅 2. 컴포넌트가 사라질 때 코드를 실행시킬 수도 있음 
    // useEffect훅 3. 여러개를 사용하고 싶다면 유즈이펙트를 다수 사용도 가능하다.(적은 순서대로 실행됨)
    // useEffect훅 4. [실행조건] : 스테이트가 위에 있어야함 -> 특정 state가 변경될 때만 실행해주세요. 
    // useEffect훅 5. [] 비어져 있을 경우 _가 변경될 때만 useEffect를 실행해 주세요. -> 페이지 첫 로드(등장) 시 한 번 실행하고 끝남

    let { id } = useParams();
    let 찾은상품 = props.shoes.find((상품)=>{return 상품.id == id})
    let history = useHistory(); //방문 기록이 담겨 있는 오브젝트
    return (
        <div className="container">
            <박스>
                <제목 className="red">상세페이지</제목>
            </박스>
            <input onChange={(e)=>{setInputData(e.target.value)}} />
            {inputData}
            <div className="my-alert">
                <p>재고 얼마 안남음</p>
            </div>
            {
                alert === true
                ? (<div className="my-alert2" >
                        <p>재고 얼마 안남음</p>
                    </div>)
                : null
            }
            <div className="row">
                <div className="col-md-6">
                    <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                {/* <h4 className="pt-5">{props.shoes[id].title}</h4> */}
                    <h4 className="pt-5">{찾은상품.title}</h4>
                    <p>{찾은상품.content}</p>
                    <p>{찾은상품.price}원</p>
                    
                    <Info 재고={props.재고}/>

                    <button className="btn btn-danger" onClick={()=>{ props.재고변경([9,11,12]) }}>주문하기</button> 
                    <button className="btn btn-danger" onClick={()=>{ history.goBack(); }}>뒤로가기</button> 
                </div>
            </div>
        </div>  
    )
};

function Info(props){
    return(
        <p>재고 :{props.재고[0]}</p>
    )   
}

export default Detail 


